import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import HomeEN from '../views/HomeEn.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/EN',
    name: 'HomeEN',
    component: HomeEN
  },
]

const router = new VueRouter({
  routes
})

export default router
